import React, { useState, useEffect } from "react"
import { ThemeProvider } from "styled-components"

// redux things
import { createStore } from "redux"
import { Provider } from "react-redux"
import rootReducer from "reducers"
import { useSelector } from "react-redux"

import { light, dark } from "assets/theme"

import GlobalStyles from "components/GlobalStyles"

import Header from "./Header"
import Body from "./Body"
import Footer from "./Footer"

const Layout = ({ children }) => {
  // global이란 이름의 reducer의 정보를 가져옵시다
  const themeMode = useSelector(state => state.theme)

  let isSystemDarkMode = null
  if (typeof window !== "undefined") {
    isSystemDarkMode = window.matchMedia("(prefers-color-scheme: dark)").matches
  }

  let localTheme = null
  if (typeof localStorage !== "undefined") {
    localTheme = localStorage.getItem("theme")
  }

  // const [themeMode, setThemeMode] = useState(
  //   localTheme ? localTheme : isSystemDarkMode ? "dark" : "light"
  // )

  const [theme, setTheme] = useState(light)

  useEffect(() => {
    if (themeMode === "light") setTheme(light)
    else setTheme(dark)
  }, [themeMode])

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      <Header />
      <Body>{children}</Body>
      <Footer />
      <button> HI </button>
    </ThemeProvider>
  )
}

export default Layout
